.invitation
{
    position: relative;
    z-index: 10;

    -webkit-box-sizing: padding-box;

            box-sizing: padding-box;
    max-width: 800px;
    margin: 50px auto;
    padding: 0 25px;
}

.invitation_header
{
    display: block;

    max-width: 90%;
    margin: 0 auto 25px auto;
}

.invitation_subheader
{
    margin-bottom: 25px;

    text-align: center;

    font-size: 22px;
}

.invintation_text
{
    max-width: 600px;
    margin: auto;
    margin-bottom: 15px;

    font-size: 22px;
    line-height: 28px;
}

.invintation_text a
{
    white-space: nowrap;
}

.invintation_disclamer
{
    max-width: 600px;
    margin: auto;
    margin-bottom: 25px;

    text-align: justify;

    font-size: 14px;
    line-height: 16px;
}

.invintation_program
{
    max-width: 600px;
    margin: auto;

    font-size: 22px;
    line-height: 34px;
}

.invitation ul
{
    padding-left: 25px;
}

.invitation li
{
    list-style-image: url(data:image/png;base64,);
}

.invitation li:hover
{
    list-style-image: url(data:image/png;base64,);
}

.invintation_links
{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.invintation_link
{
    display: block;

    width: 150px;
    padding-top: 64px;

    text-align: center;

    background-repeat: no-repeat;
    background-position: center 0;
}

.invitation_dragging_false .droptarget
{
    -webkit-transform: translateY(100%);
        -ms-transform: translateY(100%);
            transform: translateY(100%)
}

.invintation_link_wishlist
{
    background-image: url(data:image/gif;base64,);
}

.invintation_link_steam
{
    background-image: url(data:image/gif;base64,);
}

.invintation_link_chat
{
    background-image: url(data:image/gif;base64,);
}


